<template>
    <div>
        <div class="bg">
            <div class="header">
                <van-icon name="arrow-left" @click="routeBack" />
                <span>书单详情</span>
            </div>
            <div class="middle">
                <div class="title">{{ bookListDetail.title }}</div>
                <p class="desc">{{ bookListDetail.desc }}</p>
                <div class="author" v-if="bookListDetail.author">
                    <div class="img">
                        <img
                            :src="
                                'http://statics.zhuishushenqi.com' +
                                    bookListDetail.author.avatar
                            "
                        />
                    </div>
                    <div class="content">
                        <div class="main-info">
                            <span class="nickname">{{
                                bookListDetail.author.nickname
                            }}</span>
                            <span class="lv-tag"
                                >Lv.{{ bookListDetail.author.lv }}</span
                            >
                        </div>
                        <div class="others">
                            <span class="time"
                                >创建于{{
                                    timeFormat(bookListDetail.created)
                                }}</span
                            >
                            <span class="point"></span>
                            <span class="readed">{{
                                changeVal(bookListDetail.readCount)
                            }}</span>
                        </div>
                    </div>
                </div>
            </div>
            <ul class="books">
                <BookCard
                    v-for="(item, index) in bookListDetail.books"
                    :key="index"
                    :item="item.book"
                ></BookCard>
            </ul>
        </div>
    </div>
</template>

<script>
import { getData } from "@/api/api.js";
import store from "@/store/index.js";
import BookCard from "@/components/BookCard.vue";
export default {
    name: "ListDetail",
    components: {
        BookCard,
    },
    data() {
        return {
            bookListDetail: {},
        };
    },
    methods: {
        timeFormat(time) {
            let date = new Date(time);
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDay();

            month > 10 ? month : "0" + month;
            day > 10 ? day : "0" + day;

            return year + "年" + month + "月" + day + "日";
        },
        changeVal(val) {
            return (val / 10000).toFixed(1) + "万阅读";
        },
        routeBack() {
            this.$router.push({ name: "BookList" });
        },
    },
    beforeRouteEnter(to, from, next) {
        let bookListDetail = JSON.parse(
            localStorage.getItem("list-" + to.params.id)
        );

        if (bookListDetail && bookListDetail.expires > Date.now()) {
            next((vm) => {
                vm.bookListDetail = bookListDetail.data.bookList;
            });
        } else {
            store.state.loading = true;
            getData("http://novel.kele8.cn/booklists/" + to.params.id)
                .then((res) => {
                    localStorage.setItem(
                        "list-" + to.params.id,
                        JSON.stringify({
                            expires: Date.now() + 5 * 24 * 60 * 60 * 1000,
                            data: res.data,
                        })
                    );
                    store.state.loading = false;
                    next((vm) => {
                        vm.bookListDetail = res.data.bookList;
                    });
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        }
    },
};
</script>

<style lang="less" scoped>
@main-color: #fff;
.bg {
    max-width: 100vw;
    overflow-x: hidden;
    background-color: #f4f4f4;
    text-align: left;
    .header {
        background-color: @main-color;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 10px 5px;
        color: #e4393c;
        span {
            margin-left: 5px;
        }
    }
    .middle {
        background-color: @main-color;
        padding: 10px 5px;
        margin-bottom: 10px;
        .title {
            font-weight: bold;
            font-size: 16px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        .desc {
            color: #999;
            font-size: 13px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        .author {
            display: flex;
            align-items: center;
            .img {
                flex: 1;
                display: flex;
                justify-content: center;
                img {
                    width: 100%;
                    vertical-align: middle;
                }
            }
            .content {
                flex: 11;
                margin-left: 10px;
                .main-info {
                    color: #999;
                    display: flex;
                    align-items: center;
                    margin-bottom: 5px;
                    .nickname {
                        font-size: 13px;
                    }
                    .lv-tag {
                        margin-left: 10px;
                        font-size: 12px;
                        padding: 1px 3px;
                        color: #e4393c;
                        background-color: lightblue;
                        border-radius: 20px;
                    }
                }
                .others {
                    color: #999;
                    font-size: 13px;
                    display: flex;
                    align-items: center;
                    .point {
                        display: inline-block;
                        width: 3px;
                        height: 3px;
                        margin: 0 5px;
                        background-color: #999;
                        border-radius: 50%;
                    }
                }
            }
        }
    }
    .books {
        background-color: @main-color;
    }
}
</style>
